.sendForm {
    max-width: 1142px;
    margin: 56px auto 0;
    display: flex;
    gap: 44px;
}

.textarea {
    max-width: 976px;
    width: 100%;
    height: 40px;
    border-radius: 20px;
    background: #F5F7FB;
    box-shadow: 0px 5px 20px 0px rgba(29, 33, 38, 0.03), 0px 1px 2px 0px rgba(29, 33, 38, 0.10);
    border: none;
    resize: none;

    padding: 12px 24px;
    color: rgba(0, 0, 0, 0.60);
    font-family: Montserrat, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    opacity: 0.9;
}

.button {
    border: none;

    width: 120px;
    height: 40px;
    border-radius: 20px;
    background-color: #06C;

    color: #FFF;
    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.button:hover {
    cursor: pointer;
}
.button:active {
    background-color: #7ca9d7;
}
