.friendMessage {
    padding-left: 70px;
    width: 100%;
    margin-bottom: 30px;

    position: relative;
}

.friendImageAndText {
    display: flex;
    flex-direction: row;

    min-width: 462px;
    height: 77px;
}

.friendImageAndText img {
    margin-top: 12px;
    margin-right: 12px;
    width: 48px;
    height: 48px;

    border-radius: 48px;
}

.friendText {
    padding: 7px 13px;

    min-width: 414px;
    height: 60px;

    background-color: #F5F7FB;
    filter: drop-shadow(0px 1px 2px rgba(29, 33, 38, 0.10)) drop-shadow(0px 5px 20px rgba(29, 33, 38, 0.03));
}

.friendText:after {

}

.friendName {
    color: #000;
    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.friendMessageText {
    margin-top: 3px;

    color: #000;
    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.friendTime {
    margin-left: 81px;
    position: absolute;
    left: 0;
    bottom: 0;

    color: #000;
    font-family: Montserrat, sans-serif;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}
