.sidebar {
    width: 315px;
    height: 100vh;
    background-color: #ffdfdf;
    position: absolute;
    transform: translateX(-100%);
    z-index: 999;
    transition: 0.5s;
}

.sidebar.open {
    transform: translateX(0%);
}

.background {
}

.nav {
    display: flex;
    flex-direction: column;
    font-size: 1.5rem;
    margin-left: 60px;
    margin-top: 20px;
    gap: 20px;
}

.nav a {
    text-decoration: none;
}

.nav a:hover {
}

.nav a.active {
    color: red;
}
.close {
    margin-left: 90%;

