.message {
    padding-right: 70px;
    width: 100%;
    margin-bottom: 30px;

    position: relative;

}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;

    height: 77px;
}

.imageAndText img {
    margin-top: 12px;
    margin-left: 12px;

    width: 48px;
    height: 48px;
    border-radius: 48px;
}

.text {
    padding: 7px 13px;
    height: 60px;

    border-radius: 10px;
    background-color: #06C;
    filter: drop-shadow(0px 1px 2px rgba(29, 33, 38, 0.10)) drop-shadow(0px 5px 20px rgba(29, 33, 38, 0.03));
}

.text:after {

}

.name {

    display: flex;
    flex-direction: row-reverse;
    color: #FFF;
    font-family: Montserrat, sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.messageText {
    margin-top: 3px;
    display: flex;
    flex-direction: row-reverse;

    color: #FFF;
    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.time {
    margin-right: 81px;
    position: absolute;
    right: 0;
    bottom: 0;

    color: #000;
    font-family: Montserrat, sans-serif;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}